Office.onReady((info) => {
  if (info.host === Office.HostType.Outlook) {
    document.getElementById("app-body").style.display = "block";
    updateItemInfo();
    
    // Update information when a new email is selected
    Office.context.mailbox.addHandlerAsync(Office.EventType.ItemChanged, updateItemInfo);
  }
});

function updateItemInfo() {
  const item = Office.context.mailbox.item;
console.log(item.from);

  // Update the content with email details
  document.getElementById("item-subject").textContent = item.subject;
  document.getElementById("item-sender").textContent = item.from.emailAddress;
  document.getElementById("item-id").textContent = item.itemId;
}

















